import React from "react";
import { Container, Row, Col, Accordion } from "react-bootstrap";
import { StaticImage } from "gatsby-plugin-image";

const Faq = ({ faqData }) => {
  return (
    <div className="faq_outline">
      <Container>
        <Row className="faq_row">
          <Col
            xxl={6}
            xl={6}
            lg={6}
            md={12}
            sm={12}
            className="d-flex justify-content-center align-items-center align-items-lg-end "
          >
            <StaticImage
              className="faq_lhs"
              imgClassName="faq_lhs_image"
              src="../../styles/assets/images/recentworks/faq.webp"
              alt="frequently asked question"
            />
          </Col>
          <Col xxl={6} xl={6} lg={6} md={12} sm={12}>
            <div className="faq_rhs">
              <h2 className="faq_title">
                Resolve your unanswered queries effortlessly
              </h2>
              <div className="faq_acc_wrapper">
                <Accordion>
                  {faqData.map((item, index) => (
                    <Accordion.Item key={index} eventKey={index.toString()}>
                      <Accordion.Header>{item.question}</Accordion.Header>
                      <Accordion.Body>{item.answer}</Accordion.Body>
                    </Accordion.Item>
                  ))}
                </Accordion>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Faq;
